@import 'settings';
@import 'font';

body, html {
  width: 100%;
  height: 100%;
  font-size: 1em;
  color: #515353;
  font-style: normal;
  position: relative;
  font-family: 'Chaparral', sans-serif;
}

body:not(.index) {
  background: $color-grey-light;
}

* {
  box-sizing: border-box;
}

a, a:link, a:visited {
  color: $color-green;
  text-decoration: none;
}

a:hover {
  color: lighten($color-green, 10%);
}

.page-width-holder {
  width: $page-width;
  max-width: $page-width;
  margin: 0 auto;
}
#nav-buttons{
  padding: 25px 0;
}
#szalag {
  background: $color-grey-dark;
  color: white;
  padding: 25px 0;

  .holder {
    position: relative;
    font-size: 3.3em;
    text-align: center;
    .title {
      padding-right: 50px;
      margin-bottom: 25px;
    }
    .subtitle {
      color: $color-green;
      font-size: 0.6em;
      padding-left: 30px;
    }

    &:before {
      content: ",,";
      position: absolute;
      left: 10px;
      bottom: -20px;
      font-size: 240px;
      line-height: 1;
      font-family: $font-pr;
      letter-spacing: -20px;
      pointer-events: none;
    }
    &:after {
      content: ",,";
      position: absolute;
      right: 30px;
      top: -160px;
      font-size: 240px;
      line-height: 1;
      font-family: $font-pr;
      letter-spacing: -20px;
      pointer-events: none;
    }
  }
}


#login-container {
  background: $page-login-bg;
  color: white;
  padding: 10px;
  text-align: right;


  #login-box {
    font-size: 0.8em;
    label, input, button {
      height: 25px;
      line-height: 25px;
      vertical-align: middle;
      display: inline-block;
      *display: inline;
      zoom: 1;
      font-family: $font-sec;
    }

    label {
      padding:0 5px 0 10px;
      font-weight: bold;
    }

    input {
      padding: 0 10px;
      border: none;
      -webkit-box-shadow: inset 0 2px 10px -5px;
      -moz-box-shadow: inset 0 2px 10px -5px;
      box-shadow: inset 0 2px 10px -5px;
    }

    button {
      background: #515352;
      color: white;
      padding: 0 15px;
      border: none;
      cursor: pointer;
    }
  }

  &.logged > #login-box {
    display: table;
    width: 100%;

    #logged-name, nav#user_menu  {
      display: table-cell;
      vertical-align: middle;
    }
    #logged-name{
      float: right;
      font-size: 1.3em;

      a {
        background: #515352;
        color: white;
        margin: 2px;
        padding: 4px;
        font-size: 0.7em;
        font-family: $font-sec;
      }
    }
    nav#user_menu {
      float: left;
      margin: 0 -10px;

      ul, li {
        margin: 0;
        padding: 0;
        list-style: none;
      }

      ul {
        display: table;
        direction: rtl;

        li {
          display: table-cell;
          vertical-align: middle;
          padding: 0 5px;

          a {
            background-position: left 36px;
            display: block;
            width: 35px;
            height: 35px;
            text-indent: -1000em;
            overflow: hidden;
          }

          &.companies a {
            background-image: url(/images/menu_companies.png);
          }

          &.groups a {
            background-image: url(/images/menu_groups.png);
          }

          &.levlists a {
            background-image: url(/images/menu_levlists.png);
          }

          &.newsletter a {
            background-image: url(/images/menu_newsletter.png);
          }

          &.forums a {
            background-image: url(/images/menu_forums.png);
          }

          &.calendar a {
            background-image: url(/images/menu_calendar.png);
          }

          &.users a {
            background-image: url(/images/menu_users.png);
          }

          &.articles a {
            background-image: url(/images/menu_articles.png);
          }

          &:hover a,
          & > a.selected {
            background-position: left 0px;
          }
        }
      }
    }
  }
}


.left {
  float: left;
  display: inline-block;
  *display: inline;
  zoom: 1;
}
.right {
  float: right;
  display: inline-block;
  *display: inline;
  zoom: 1;
}

.newsletter-items{
  margin: 20px 0;
  .left {
    width: 40%;

    ul{
      max-height: 400px;
      overflow: auto;
    }
  }
  .right {
    width: 60%;
  }

  .connected-articles{
    border: 5px solid #f1f1f1;
  }

  ul {
    background: #cdcdcd;
  }
  ul, li {
    list-style: none;
    margin: 0 !important; padding: 0;
  }

  li .item-content {
    display: none;
  }

  li {
    background: #ffffff;
    margin: 5px !important;
    padding: 5px !important;
    cursor: move;
     h3 {
      font-size: 1em;
      margin: 0 !important;
     }
  }

  .selected-items{
    padding: 0 0 0 25px;
  }

  .selected-items-content{
    min-height: 400px;
    background: #ffffff;
    border: 5px solid #f1f1f1;
    padding: 10px;

    h2 {
      border-bottom: 1px solid #e1e1e1;
      margin: 0 0 10px 0;
      padding: 0 0 10px 0;
    }

    ul {
      border: none;
      min-height: 350px;
      background: #ffffff;

      li {
        float: left;
        width: 50%;
        margin: 0 -1px !important;

        h3 {
          font-weight: bold;
          font-size: 1.2em;
        }

        .item-content {
          display: block;
        }
      }
    }
  }
}

form{
  dl{
    display: block;
    width: 100%;


    label {
      font-weight: bold;
      font-size: 0.9em;
      color: black;
      margin: 10px 0 5px 0;
      display: block;
    }

    input[type=text],
    input[type=password],
    input[type=number],
    input[type=tel],
    input[type=email],
    textarea {
      width:  100%;
      line-height: 35px;
      padding: 0 8px;
      font-size: 1.2em;
      background: #f8f8f8;
      border: 1px solid #dadada;
    }


    dt, dd {
      margin: 4px 0;
      padding: 0;
      width: 100%;

      &.radio {
        float: left;
        width: auto;
        height: 25px;
        line-height: 25px;
        vertical-align: middle;
        padding: 0 3px;
        clear: none;

        label {
          margin: 0;
          line-height: 25px;
          height: 25px;
        }
      }
    }

  }
}
#calendar-list{
  .eventitem{
    margin: 0 0 10px 0;
    padding: 0 0 10px 0;
    border-bottom: 1px solid white;

    h3 {
      margin-bottom: 8px;
      font-size: 1.3em;
    }
  }
}
.eventlist{
  .eventitem {
    margin: 0 0 25px 0;
    padding: 0 0 25px 0;
    border-bottom: 1px solid $color-grey-dark;

    h3 {
      font-size: 1.8em;
      margin: 0 0 25px 0;
    }
  }
}
#calendar-content{
  background: #222222;

  .calendar-pager{
    text-align: center;
    padding: 10px;
    font-size: 1.3em;
    color: white;
    font-weight: bold;

    a {
      color: $color-green;
    }

    .year-next{
      margin-right: 8px;
    }
    .month-prev{
      margin-left: 8px;
    }
  }

  table {
    th {
      font-size: 0.9em;
    }
    td {
      padding: 5px 2px;
      text-align: center;
      background: #222;
      color: #444444;
    }
    td.current {
      background: #343434;
      color: #999999;
    }
    td.hasevent {
      background: $color-red;

      a {
        color: white;
        display: block;
      }
    }
  }
}
.message{
  -webkit-border-radius:  0 !important;
  -moz-border-radius: 0 !important;
  border-radius:  0 !important;
  padding: 10px !important;

  .right, .left {
    float: none;
    text-align: left;
  }

  &:after {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    clear: both;
  }
}
.error {
  background: $color-red;

}
.chzn-container-single .chzn-single div b{
  background-position: 0 5px;
}
.chzn-container-active .chzn-single-with-drop div b{
  background-position: -18px 5px;
}
.chzn-container {
  width: 100% !important;

  .chzn-single {
    line-height: 36px;
    font-size: 1.2em;
    height: 36px;
    background: #f8f8f8;
    border: 1px solid #dadada;
    -moz-border-radius: 0;
    -webkit-border-radius: 0;
    border-radius: 0;
  }
  .chzn-drop{
    width: 100% !important;
  }
  .chzn-search {
    input {
      width: 100% !important;
      height: 25px;
    }
  }
  .chzn-results{
    margin: 0 !important;
  }
}

.right_cont{
  & > div {
    display: inline-block;
    *display: inline;
    zoom: 1;
    padding: 0 5px;

    label {
      display: inherit;
    }
  }
}

table.table {
  border-collapse: collapse;
  thead {
    th {
      background: $color-grey-light;
      padding: 5px;
      font-size: 0.8em;
      color: $color-grey-dark;
    }
  }

  tbody {
    tr {
      td {
        border-right: 1px solid $color-grey-light;
        padding: 5px;
      }
      &:nth-child(even){
        background: #f2f2f2;
      }
    }
  }

  &.sortable {
    tbody tr {
      cursor: row-resize;
    }
  }
}

.content-holder{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 -10px;
  padding-bottom: 20px;

  .fullpage {
    flex-basis: 100%;
    margin-top: 25px;
  }

  section {
    padding: 0 10px;
  }

  section#forum-left, section.list-left {
    flex-basis: 70%;

    h2.page_header {
      text-align: left;
      margin: 25px 0;
    }
  }
  section#forum-right, section.list-right {
    flex-basis: 30%;

    .forum-lead {
      color: white;
      text-align: justify;
      h3 {
        text-transform: uppercase;
        margin: 0 0 10px 0;
        font-size: 2em;
        text-align: center;
      }

      .description{
        line-height: 1.2;
        font-size: 0.9em;
        font-style: italic;
      }
    }

    form {
      .form-row {
        margin: 5px 0;

        label {
          padding: 4px 0;
          font-weight: bold;
          display: block;
        }

        input {
          width: 100%;
          display: block;
          margin: 4px;
          padding: 5px;
          border: none;
        }
      }
    }

    & > section {
      background: rgba(0, 0, 0, 0.77);
      margin-top: -8px;
      padding: 15px;
      color: #cecece;
      font-size: 0.9em;

      section {
        padding: 0;
      }

      & > h3 {
        color: white;
        margin: 10px 0;
      }

      & > ul {
        margin-left: 20px;
      }

      article {
        border-bottom: 1px solid white;
        margin: 10px 0;
        padding: 10px 0;

        h3 {
          margin: 0 0 10px 0;
          a {
            color: white;
            font-size: 1.2em;
            &:hover {
              color: $color-green;
            }
          }
        }

        header {
          font-family: $font-sec;
          margin: 15px 0 ;
          font-size: 0.8em;
          padding: 5px 0;
          border: 1px solid #7d7d7d;
          border-left: none;
          border-right: none;
          font-weight: bold;

          span {
            color: $color-green;
          }

          a {
            color: #ffffff !important;

            &:hover {
              color: $color-red !important;
            }
          }
        }

        .more {
          text-align: right;

          a {
            background: $color-red;
            padding: 8px 10px;
            color: white;
            text-transform: uppercase;
            font-family: $font-sec;
            font-weight: bold;
            font-size: 0.8em;
            display: block;
            float: right;
          }
        }

        &:last-child{
          border-bottom: none;
        }
      }
    }

    h2.page_header{
      background: $color-green;
      color: white;
      margin: 20px -15px 10px -15px;
      font-size: 2em;
      padding: 20px;
    }
  }

  .content-page {
    background: white;
    padding: 20px;
    color: $color-content;

    ul {
      margin: 10px 0 10px 20px;
    }

    #editor_leader {
      color: darken($color-content, 10%);
      margin-bottom: 25px;
    }

    h1, h2, h3, h4, h5 {
      font-family: $font-pr;
      color: $color-green;
      margin: 5px 0 10px 0;
      font-weight: initial;
    }

    p {
      margin: 10px 0;
    }
  }
}

