      text-indent: -1000em;
      width: 240px;
      height: 60px;
      margin: 0 auto;
      background-image: url(/images/logo.png);
      background-repeat: no-repeat;
    }
  }
  #topmenu {
    text-align: center;
    border-top: 1px solid #E6E7E8;

    ul, li {
      list-style: none;
      margin: 0;
      padding: 0;
    }
    ul > li {
      display: inline-block;
      *display: inline;
      zoom: 1;
      vertical-align: middle;
      position: relative;

      & > a {
        display: block;
        padding: 20px;
        color: $base-color;
        text-decoration: none;
        font-size: 1.1em;
        border-top: 3px solid transparent;

        &:hover {
          border-top: 3px solid $color-green;
        }
      }

      & > ul {
        position: absolute;
        z-index: 1000;
        display: none;
        background: $color-grey-dark;
        width: 250px;
        max-width: 250px;
        li {
