          color: white;
          text-decoration: none;
          font-size: 0.8em;
          text-align: left;
          padding: 10px;
          line-height: 1.4;
          border-bottom: 1px solid #6e6e6e;

          &:hover {
            background: $color-green;
          }
        }
      }

      &:hover > ul {
        display: block;
      }
    }
  }
}

