          line-height: 1.1;
          font-weight: normal;
        }

        .more {
          text-align: right;

          a {
            background: $color-red;
            padding: 8px 10px;
            color: white;
            text-transform: uppercase;
            font-family: $font-sec;
            font-weight: bold;
            font-size: 0.8em;
            display: block;
            float: right;
          }
        }
      }
    }

    &.grid-x3 > article{
      flex-basis: calc(100%/3);
    }
    &.grid-x5 > article{
      flex-basis: calc(100%/5);
    }
  }

  &.post-list-default{
    article {
      .iwrapper {
        background: white;
      }
    }
  }

  &.forum-home-view{
    article {
      padding: 20px;

      h3 {
        height: 55px;
        max-height: 55px;
        a {
          color: black;
          font-size: 1.3em;

          &:hover {
            color: $color-green;
          }
        }

        margin: 0 0 10px 0;
      }

      a.more {
        margin: 15px 0 0 0;
        background: $color-green;
        padding: 8px 10px;
        color: white;
        text-transform: uppercase;
        font-family: $font-sec;
        font-weight: bold;
        font-size: 0.8em;
        display: block;
        float: left;

        &:hover {
          background: $color-grey-dark;
        }
      }
    }
  }
}

#nav-buttons {
  .holder {
    ul, li {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    ul {
      display: table;
      position: relative;
      margin: 0 -10px;
    }
    li {
      display: table-cell;
      position: relative;
      padding: 10px;
      height: 115px;
      width: calc(100%/3);
      float: left;
      vertical-align: middle;


      a {
        display: table;
        width: 100%;
        height: 100%;
        font-size: 1.55em;
        padding: 0 25px;
        text-align: center;
        background: $color-green;
        color: white;
        line-height: 1.3;

        & > div {
          display: table-cell;
          vertical-align: middle;
        }

        &:hover {
          background: $color-grey-dark;
        }
      }
    }
  }
}

img {
  position: relative;
  max-width: 100%;
  border: none;
  height: auto !important;
  max-height: initial !important;
}

#share {
  .buttons {
    .button {
      display: inline-block;
      *display: inline;
      zoom: 1;
      padding: 10px;
      vertical-align: top;

      &.googleplus {
        max-width: 62px;
        overflow: hidden;
      }
    }
  }
}

#comments-container{
  margin-top: 20px;
  h3.header {
    font-family: $font-sec;
    color: $color-grey-dark;
    text-transform: uppercase;
    font-size: 1.8em;
  }
  form {
    .form-row {
      margin-bottom: 8px;

      textarea {
        width: 100%;
        background: #f2f2f2;
        border: 1px solid #e1e1e1;
        padding: 15px;
      }
    }
  }

  .article-comment{
    border-left: 5px solid $color-grey-light;
    padding-left: 15px;
    margin-bottom: 8px;

    h3 {
      margin: 0 0 4px 0;
      a {
        color: $color-grey-dark;
        font-family: $font-sec;
        font-weight: bold;

        &:hover {
          color: $color-green;

        }
      }
    }
    header {
      font-size: 0.8em;
      margin-bottom: 10px;
      font-family: $font-sec;
    }
    .comment-content{
      font-size: 1.2em;
      color: #4d4d4d;
      line-height: 1.2;
    }
  }
}

button[type=submit] {
  background: $color-green;
  padding: 10px 20px;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  margin: 5px 0;
  border: none;
  cursor: pointer;

  &:hover {
    background: $color-grey-dark;
  }
}

#tooltip {
    position: absolute;
    display: none;

    top: 0;
    left: 0;

    padding: 5px 10px;
    background-color: #dedede;

    border: 1px solid #d1d1d1;

    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    border-radius: 8px;
}

.pager tr td a {
  background: darken($color-grey-light, 20%);
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
}
.pager tr td a:hover, .pager tr td a.current{
  background: $color-green;
  color: white;
  border-color: darken($color-green, 10%);
}

#slideshow-container{
  position: relative;
  display: block;
  width: 100%;
  height: $slideshow-height;
  max-height: $slideshow-height;
  overflow: hidden;
  background: darken($color-green, 10%);
  border-bottom: 20px solid darken($color-green, 15%);

  #slideshow-items{
    position: relative;
    height: 100%;
    max-height: 100%;
    overflow: hidden;
    .slideshow-item{
      position: absolute;
      display: inline-block;
      *display: inline;
      zoom: 1;
      float: left;
      height: 100%;
      width: 100%;
      background-position: top center;
      background-repeat: no-repeat;
      background-size: cover;

      & > div {
        width: $page-width;
        margin: 0 auto;
        color: white;
        padding: 5%;
        text-align: center;

        h2 {
          text-align: center;
          font-size: 3em;
          font-weight: normal;
          margin-bottom: 20px;
        }

        p {
          font-size: 1.2em;
        }
      }
    }
  }

  #slideshow-left,
  #slideshow-right{
    position: absolute;
    z-index: 1000;
    height: 100%;
    width: 50px;
    text-align: center;

     a {
       position: relative;
       background: rgba(255, 255, 255, 0.0);
       display: block;
       height: 100%;
       opacity: 0.3;
       filter: alpha(opacity=30);
        text-indent: -1000em;

       &:before {
         position: absolute;
         content: "";
         background-image: url("/images/slide-arrows.png");
         background-repeat: no-repeat;
         width: 25px;
         height: 32px;
         top: 50%;
         left: 50%;
         -webkit-transform: translate(-50%, -50%);
         -moz-transform: translate(-50%, -50%);
         transform: translate(-50%, -50%);
       }
     }

     &:hover a {
       background: rgba(255, 255, 255, 0.21);
       opacity: 1;
       filter: alpha(opacity=100);
     }
  }
  #slideshow-left{
    left: 0;
    top: 0;

    a:before {
      background-position: 0 0;
    }
  }
  #slideshow-right{
    right: 0;
    top: 0;

    a:before {
      background-position: -25px 0;
    }
  }
}

.show-on-mobile {
 display: none;
}

@import 'responsive';
